  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* End of CSS reset */

:root {
  --bg-color-dark: hsl(50, 100%, 80%);
  --bg-color: hsl(50, 100%, 60%);
  --bg-color-light: hsl(50, 100%, 40%);

  --text-color: hsl(50, 15%, 15%);
}

body, html {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   height: 100%;
   font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   background: linear-gradient(hsl(52, 100%, 50%), hsl(45, 100%, 50%));
   color: var(--text-color);
}

.upcoming-weather-cards {
  display: grid;
  grid-template-columns: repeat(5, 200px);
  grid-template-rows: 150px;
  gap: 1rem;
}

.content {
  width: 1160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0px 0px 15px black;
}

.middle-row {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 2fr, 1fr;
  grid-template-rows: 400px;
  gap: 1rem;
}

.content > * {
  margin-bottom: 1rem;
}

img {
  align-self: center;
  width: 480px;
  height: 400px;
  border-radius: 100px;
}

.todays-weather, .weather-card {
  display: grid;
  grid-template-rows: min-content min-content min-content min-content min-content;
  border: 1px solid black;
  border-radius: 1rem;
  padding: 1rem;
  box-shadow: 0px 3px 3px black;
  background: #FFF;
  transition: 500ms;
  background: var(--bg-color);
}

.todays-weather {
  width: 600px;
}

.todays-weather:hover, .weather-card:hover {
  transform: translateY(-5px);
  transition: 500ms;
  box-shadow: 0px 2px 7px black;
}

#search-city {
  width: 600px;
  height: 30px;
  border-radius: 1rem;
  border: 1px solid var(--bg-color-light);
  padding-left: 0.5rem;

}

#search-button {
  border-radius: 2rem;
  background: none;
  width: 1.75rem;
  height: 1.75rem;
  border: 1px solid var(--bg-color-light);
  position: relative;
  cursor: pointer;
  align-self: center;
}

#search-button:hover {
  border: 1px solid var(--bg-color);
}

.card-date {
  justify-self: end;
  align-self: end;
  color: rgb(50, 46, 46);
}

.content {
  border: 1px solid black;
  padding: 3rem;
  border-radius: 2rem;
  background: var(--bg-color-dark);
}

.searchImg {
  position: absolute;
  width: 1rem;
  height: 1.5rem;
  top: 0;
}

.search-section {
  position: relative;
  margin-bottom: 2rem;
}

form {
  display: flex;
  gap: 0.5rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;;;EAGE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;;;;;EAKE,cAAc;EACd,eAAe;AACjB;;AAEA;;;;EAIE,aAAa;AACf;;AAEA;;;;;;;EAOE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;;AAEA;;;;;;EAME,kBAAkB;AACpB;;AAEA;;EAEE,kBAAkB;AACpB;;AAEA,qBAAqB;;AAErB;EACE,mCAAmC;EACnC,8BAA8B;EAC9B,oCAAoC;;EAEpC,+BAA+B;AACjC;;AAEA;GACG,aAAa;GACb,sBAAsB;GACtB,uBAAuB;GACvB,mBAAmB;GACnB,YAAY;GACZ,yFAAyF;GACzF,mEAAmE;GACnE,wBAAwB;AAC3B;;AAEA;EACE,aAAa;EACb,uCAAuC;EACvC,yBAAyB;EACzB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,oCAAoC;AACtC;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,+BAA+B;EAC/B,yBAAyB;EACzB,SAAS;AACX;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,aAAa;EACb,+EAA+E;EAC/E,uBAAuB;EACvB,mBAAmB;EACnB,aAAa;EACb,6BAA6B;EAC7B,gBAAgB;EAChB,iBAAiB;EACjB,2BAA2B;AAC7B;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,2BAA2B;EAC3B,iBAAiB;EACjB,6BAA6B;AAC/B;;AAEA;EACE,YAAY;EACZ,YAAY;EACZ,mBAAmB;EACnB,uCAAuC;EACvC,oBAAoB;;AAEtB;;AAEA;EACE,mBAAmB;EACnB,gBAAgB;EAChB,cAAc;EACd,eAAe;EACf,uCAAuC;EACvC,kBAAkB;EAClB,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE,iBAAiB;EACjB,eAAe;EACf,sBAAsB;AACxB;;AAEA;EACE,uBAAuB;EACvB,aAAa;EACb,mBAAmB;EACnB,gCAAgC;AAClC;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,cAAc;EACd,MAAM;AACR;;AAEA;EACE,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,WAAW;AACb","sourcesContent":["/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\np {\n  text-wrap: pretty;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  tex